import { Component, ChangeDetectionStrategy, Input } from '@angular/core';
import { CollectionService } from '../common/collection.service';
import { ConfigurationService } from '../common/configuration.service';
import { InheritMetadata } from '../common/inherit-metadata';
import { CategoryAxisItemComponentGenerated } from './category-axis-item.component.generated';
import { AutoBaseUnitSteps } from '../common/property-types';

/**
 * The configuration component for a category axis.
 */
@InheritMetadata()
@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [ConfigurationService],
  selector: 'kendo-chart-category-axis-item',
  template: ''
})
export class CategoryAxisItemComponent extends CategoryAxisItemComponentGenerated {
  /**
   * The time interval steps to use when either `baseUnit` is set to `"fit"` or `"auto"`.
   *
   * The axis will try to divide the active period into the smallest possible units
   * that yield `maxDateGroups` or less discrete intervals.
   */
  @Input() public autoBaseUnitSteps: AutoBaseUnitSteps;

  constructor(protected configurationService: ConfigurationService,
              protected collectionService: CollectionService) {
    super(configurationService, collectionService);
  }
}



